<template>
  <div class="wrapper">
  <h1>{{id ?'编辑' : '新增'}}文章{{id}}</h1>
  <el-form label-width="120px" @submit.native.prevent="">
  <el-form-item label='所属分类'>
<el-select v-model="model.categories" multiple>
<el-option v-for="item in categories" :key='item._id'
:label="item.name"
:value="item._id"></el-option>
</el-select>  
  </el-form-item>
  <el-form-item label='标题' label-width='120px'>
    <el-input v-model="model.title"  style="width: 500px"></el-input>  
  </el-form-item>
  <el-form-item label='详情' label-width='120px'>
    <vue-editor v-model="model.body"
    useCustomImageHandler @image-added="handleImageAdded"></vue-editor> 
  </el-form-item>
  <el-form-item >
<el-button type='primary' native-type="submit" @click="save">保存</el-button>  
  </el-form-item>
  </el-form>
  </div>
</template>

<script>
import {VueEditor} from 'vue2-editor'
export default {
  components: {
    VueEditor
  },
  props: {
    id:{}
  },
  data() {
    return {
        model:{},
        categories:[],
        
    };
  },
  watch: {},
  computed: {},
  methods: {
   async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData();
      formData.append("file", file);
 const res= await this.$http.post("upload",formData)
 Editor.insertEmbed(cursorLocation, "image", res.data.url);
 resetUploader();
     
    },
     async save(){
      let res
       if(this.id){
         res=await this.$http.put(`rest/articles/${this.id}`,this.model)
       }
       else{
       res= await this.$http.post('rest/articles',this.model)
       }
       console.log(res.data.name);
       
       this.$router.push('/articles/list')
       this.$message({
         type:'success',
         message:'保存成功'
       })
       
      },
      async fetch(){
        const res =await this.$http.get(`rest/articles/${this.id}`)
        this.model=res.data
      },
      async fetchCategories(){
        const res =await this.$http.get(`rest/categories`)
        this.categories=res.data
      },
      
  },
  created() {
    this.fetchCategories()
    this.id && this.fetch()
  },
  mounted() {}
};
</script>
